<div><v-layout><v-app-bar id="color" absolute style="overflow: visible"><v-app-bar-nav-icon class="navbarcolor" @click="clickdrawer()"></v-app-bar-nav-icon><v-app-bar-title><a>Mes réalisations</a></v-app-bar-title></v-app-bar><v-navigation-drawer class="overflow-" id="color" v-model="drawer" absolute temporary><v-list nav dense><div v-for="item in redirectitems"><v-list-item :prepend-icon="item.icon" :title="item.title" @click="redirectnavbar(item.path)"></v-list-item></div></v-list></v-navigation-drawer><v-main><v-card class="mx-auto overflow-hidden" height="100vh" width="0vw"></v-card></v-main><div id="main"><v-card id="ecartcategorie" elevation="0"></v-card><v-card id="ecartcategorie" elevation="0"></v-card><div style="width: 90vw;"><v-card width="80%"><v-card-title><a>Mes Réalisations</a></v-card-title><v-card elevation="0	"><v-card-title><a>Last Voxes</a></v-card-title><v-card-text><p>Last Voxes est un bot discord permettant l'automatisation et la simplification de nombreuses taches administratives sur un serveur discord.</p><p>Sa structure est simple:</p><li>Un robot discord, développé avec la librairie discord.py, permettant l'utilisation simplifiée de l'API Discord, lié à une base SQLite.</li><li>Utilisation de Github pour gérer les versions.</li><br><p>En cours de finalisation :</p><li>Une API Python Flask (temporaire) permettant de communiquer avec une base de données PostgreSQL.</li><li>Une base de données PostgreSQL permettant de stocker les informations.</li><li>Une interface web, faite avec Vue.JS (framework HTML/ CSS / JS)</li><li>Docker pour créer des images, Kubernetes et Prometheus pour rendre l'API scallable en attendant de changer le langage.</li><br><p>Le code sur github est en repository privé, pour des raisons de sécurité.</p><br><p>Pour en savoir plus à propos du bot:</p><li> <a href="https://discord.gg/bbqyjUu8dB" target="_blank">Lien vers le serveur discord (création de compte requis)</a></li><li><a href="https://discord.com/api/oauth2/authorize?client_id=864281572399251488&amp;permissions=1480152508918&amp;scope=bot%20applications.commands" target="_blank">Lien pour inviter le robot (création de compte + serveur discord requis)</a></li><li> <a href="https://top.gg/bot/864281572399251488" target="_blank">Lien vers top.gg, site de recensement de robots discord</a></li><br><p>Nombre de serveurs qui l'utilisent: {{lastvoxesnumguilds}}.</p></v-card-text></v-card></v-card><v-card id="ecartcategorie" elevation="0"></v-card><v-card width="80%"><v-card-title><a>Mes contributions</a></v-card-title><v-card elevation="0"><v-card-title><a>Helper Python et Base de données</a></v-card-title><v-card-text><p>Je suis helper Python et base de données sur un serveur discord d'environ 600 membres.</p><br><p>Ce genre d'expérience m'a permis de rencontrer des développeurs en devenir, de discuter de différents projets avec eux, ce qui m'a permis<a> de rencontrer des personnes ayant de meilleures compétences que moi, qui se sont interéssées à mes projets, et m'ont énormément aidé.</a></p><p>Être helper me permet de rendre la pareille à d'autres futurs développeurs, à m'interesser à leurs projets, mais surtout, c'est pouvoir discuter autour d'un même et unique domaine.</p></v-card-text></v-card><v-card elevation="0"><v-card-title><a>Conférence</a></v-card-title><v-card-text><p>Animation d'une conférence basée sur le Python, en particulier sur la librairie discord.py, permettant de créer des robots discord.</p><br><p>J'y ai expliqué, avec un autre développeur, comment créer et coder un bot discord, librairie asynchrone, définir une instance, différentes commandes, évenements, mais surtout, comment lire et comprendre une documentation, ce qui n'est pas toujours facile !</p></v-card-text></v-card><v-card elevation="0"><v-card-title><a>Missions de prestations</a></v-card-title><v-card-text><p>Différentes missions liées au développement, par exemple:</p><li>Création d'un script permettant de modifier facilement des milliers de fichiers (JSON).</li><li>Explication de cours et d'exercices à différents étudiants.</li><li>Réalisation d'un bot python permettant de poster chaque nouvelle annonce postée sur dealabs.</li><li>etc.</li></v-card-text></v-card></v-card></div></div></v-layout></div>